<template>
  <div style="height: 100%;overflow: hidden;position: relative">
    <div class="reader__menu_container" @click="store.changeStoreByKeypath('showMenu', false)">
      <el-menu
        :class="{ menu_collapse: !store.showMenu }"
        class="reader__menu"
        @select="handleClick"
        v-loading.fullscreen.lock="store.textLoading"
        default-active="1"
      >
        <template v-for="(value, key) in store.catalog">
          <el-menu-item :key="key" :index="key">
            {{
            value
            }}
          </el-menu-item>
        </template>
      </el-menu>
    </div>
    <div class="reader__text_container">
      <span class="reader__text">{{ text }}</span>
    </div>
  </div>
</template>

<script>
import store from "@/store.js";
export default {
  name: "reader",
  data() {
    return {
      store,
      text: ""
    };
  },
  methods: {
    handleClick(e) {
      console.log(e);
      this.store.query(
        `119.45.12.80:3000/queryTxt?name=${e}`,
        res => {
          this.text = res.data;
        },
        "textLoading"
      );
    }
  }
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.reader__menu_container {
  height: 100%;
  position: absolute;
  top: 0;
  left: 0;
  z-index: 90;
}
.reader__menu {
  height: 100%;
  overflow: scroll;
}
.menu_collapse {
  transform: translateX(-100%);
}
.reader__menu:not(.el-menu--collapse) {
  width: 200px;
  transition: all 0.3s ease;
}
.reader__text_container {
  height: 100%;
  overflow-x: hidden;
  overflow-y: scroll;
  position: absolute;
  top: 0;
  left: 0;
  z-index: 50;
  padding: 20px;
}
.reader__text {
  font-size: 16px;
  white-space: pre-wrap;
}
</style>



