
class RootStore {
    loadingMap = {
        textLoading: false,
        catalogLoading: false
    };

    // 加载中的apiList，元素为cancel的function
    pendingApiList = {};
    syncQueneApiList = {};
    // 为页面中所有接口打标，用作接口的记录和操作
    pendingApiId = 1;
    syncQueneApiId = 1;
    // aciton记录
    actionTrace = {};

    showMenu = true
    catalog = {}

    changeStoreByKeypath = (keypath, value) => {
        let pathArr = keypath.split('.');
        let target;
        let targetKeyCurrent;
        let targetCurrent = this;

        while (pathArr.length) {
            targetKeyCurrent = pathArr.shift();

            // 如果当前目的变量不是引用类型，或者已经达到最后一个path，则打断
            if (typeof targetCurrent[targetKeyCurrent] !== 'object' || !pathArr.length) break;
            targetCurrent = targetCurrent[targetKeyCurrent];
        }

        // 如果目的变量未到达，则中止
        if (pathArr.length) {
            console.error('目的变量未到达，请检查', keypath);
            return;
        }

        // 修改目的变量
        target = targetCurrent;

        // 记录每次的action,大数据情况下会很消耗内存, todo
        // if (Array.isArray(this.actionTrace[keypath])) {
        //     this.actionTrace[keypath].push({ prevValue: target[targetKeyCurrent], nextValue: value });
        // } else this.actionTrace[keypath] = [{ prevValue: target[targetKeyCurrent], nextValue: value }];

        target[targetKeyCurrent] = value;
    };

    query = async (api, cb, loadingType, errBack) => {
        let res
        try {
            loadingType && this.changeStoreByKeypath(`loadingMap.${loadingType}`, true)
            res = await fetch(api, {
                mode: 'cors',
            }).then(r => r.json())
            loadingType && this.changeStoreByKeypath(`loadingMap.${loadingType}`, false)
            cb && cb(res)
        } catch (e) {
            errBack && errBack()
        } finally {

        }
    }
}
export default new RootStore();



