<template>
  <div id="app">
    <Header class="header"></Header>
    <main>
      <router-view />
    </main>
  </div>
</template>

<script>
import store from "./store.js";
import Header from "@/components/Header";
export default {
  name: "App",
  data() {
    return {
      store
    };
  },
  components: {
    Header
  },
  mounted() {
    this.store.query(
      "http://119.45.12.80:3000/queryTxt?name=a_%E7%9B%AE%E5%BD%95",
      res => {
        store.changeStoreByKeypath("catalog", res.data);
      },
      "catalogLoading"
    );
  }
};
</script>

<style>
#app {
  font-family: "Avenir", Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #2c3e50;
  position: relative;
}
body {
  background: rgb(241, 229, 201);
}
.header {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 40px;
  background: black;
}
main {
  position: absolute;
  top: 40px;
  width: 100%;
  height: calc(100vh - 40px);
  background: linear-gradient(to top, #ded1b3, 20%, rgb(241, 229, 201));
}
</style>



