<template>
  <div class="txt-header flex ali_center">
    <div class="txt__catalog" @click="store.changeStoreByKeypath('showMenu', true)">目录</div>
  </div>
</template>

<script>
import store from "@/store.js";
export default {
  name: "header",
  props: ["showMenu"],
  data() {
    return {
      store
    };
  },
  methods: {}
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.txt-header {
  color: #fff;
  padding: 0 1em;
  height: 40px;
}
.txt__catalog {
  cursor: pointer;
}
</style>



